<template>
  <vs-row v-if="!LOADING">
    <vs-col vs-type="flex" vs-w="6" style="padding-top:1em">
      <vs-breadcrumb>
        <li>
          <a href="/" title="Home" v-colorChange>Home</a>
          <span class="vs-breadcrum--separator">/</span>
        </li>
        <li aria-current="page" class="active">Discover</li>
      </vs-breadcrumb>
    </vs-col>
    <vs-col vs-type="flex" vs-w="6" style="padding-top:1em;padding-right:1em">
      <Search />
    </vs-col>
    <vs-col
      type="flex"
      vs-justify="center"
      v-for="item in discover"
      vs-align="center"
      vs-w="3"
      style="padding:1em;width:20%"
      :key="item.original_title"
    >
      <router-link :to="{ name: 'movie', params: { id: item.id }}">
        <vs-card class="cardx">
          <div slot="media">
            <img :src="item.poster_path | setImagePath" />
          </div>
          <div>
            <span>{{item.overview}}</span>
          </div>
          <div slot="footer">
            <vs-row vs-justify="flex-end">
              <vs-button type="gradient" color="danger" icon="favorite"></vs-button>
              <vs-button color="primary" icon="turned_in_not"></vs-button>
              <vs-button color="rgb(230,230,230)" color-text="rgb(50,50,50)" icon="settings"></vs-button>
            </vs-row>
          </div>
        </vs-card>
      </router-link>
    </vs-col>
    <Pagination :page="page" :totalPage="totalPage" @more="more" @prev="prev" />
  </vs-row>
</template>

<script>
import { mapState, mapGetters } from "vuex";
const Search = () => import("@/views/Search/Index.vue");
const Pagination = () => import("@/components/pagination.vue");

export default {
  computed: {
    ...mapState({
      discover: state => state.movie.discover
    }),
    ...mapGetters({
      LOADING: "contentLoaderMain",
      page: "movie/getCurrentPage",
      totalPage: "movie/getTotalPage"
    })
  },
  methods: {
    more() {
      this.$store.dispatch("movie/fetchDiscoverMore");
    },
    prev() {
      this.$store.dispatch("movie/fetchDiscoverPrev");
    }
  },
  mounted() {
    this.$store.dispatch("movie/fetchDiscover");
  },
  components: { Search, Pagination }
};
</script>

<style lang="scss">
.discover {
  .vs-breadcrumb--ol {
    .active {
      font-weight: bold;
    }
  }
}
</style>

